
import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const login =()=>import('../pages/login/login')
const index =()=>import('../pages/index/index')
// const menuAdd = () => import('../pages/menu/menuAdd')

const role = () => import('../pages/role/role')
const manger = () => import('../pages/manger/manger')
const menu = () => import('../pages/menu/menu')
const spces = () => import('../pages/spces/spces')
const loop = () => import('../pages/loop/loop')
// const seck = () => import('../pages/seck/seck')
const seck =() =>import('../pages/seck/seck')
const classify = () => import('../pages/classify/classify')
const goods = () => import('../pages/goods/goods')
const member = () => import('../pages/member/member')


export const indexRouters=[
  {
    path:"home",
    component: () => import('../pages/home/home'),
  },
  {
    path:"role",
    name:"角色管理",
    component:role,
  },
  {
    path:"manger",
    name:"管理员管理",
    component:manger,
  },
  {
    path:"loop",
    name:"轮播图管理",
    component:loop,
  },
  {
    path:"menu",
    component:menu,
    name:"菜单管理",
  },
  {
    path:"spces",
    name:"商品规格",
    component:spces,
  },
  {
    path:"seck",
    name:"秒杀活动",
    component:seck,
  },
  {
    path:"classify",
    name:"商品分类",
    component:classify,
  },
  {
    path:'goods',
    name:"商品管理",
    component:goods
  },
  {
    path:'member',
    name:"会员管理",
    component:member
  }
]

export default new Router({
  routes: [
   {
     path:"/",
     component:login
   },
   {
     path:"/index",
     component:index,
     children:[
      {
        path:'',
        redirect:'home'
      },
      ...indexRouters
     ]
   }
  ]
})



