import {reqGoodsList,reqGoodsConut} from '../../util/request'

const state= {
    list:[],
    size:2,
    total:0,
    page:1
}
const mutations= {
    changeList(state,arr){
        state.list = arr
    },
    changeTotal(state,total){
        state.total = total
    },
    changePage(state,page){
        state.page = page
    }
}
const actions= {
    requsetGoodsList(context){
        reqGoodsList({page: context.state.page, size: context.state.size }).then(res=>{
            if(res.data.code===200){
                let list = res.data.list?res.data.list:[];
                if(list.length==0&&context.state.page>1){
                    context.commit('changePage',context.state.page-1);
                    context.dispatch("requsetGoodsList")
                    return;
                }
                context.commit('changeList',list)
            }
        })
    },
    requsetGoodsConut(context){
        reqGoodsConut().then(res=>{
            if(res.data.code===200){
                const total=res.data.list[0].total
                context.commit('changeTotal',total)
            }
        })
    },
    changePageAction(context,page){
        context.commit("changePage",page);
        context.dispatch('requsetGoodsList')
    }
}
const getters= {
    list(state){
        return state.list
    },
    total(state){
        return state.total
    },
    size(state){
        return state.size
    }
}

export default{
    state,
    mutations,
    actions,
    getters,
    namespaced: true
}


