import {reqRoleList} from '../../util/request'

const state={
    roleList:[]
}

const mutations= {
    changeRoleList(state,arr){
        state.roleList=arr
    }
}

const actions= {
    requsetRoleList(context){
        reqRoleList().then(res=>{
            context.commit('changeRoleList',res.data.list)
        })
    }
}

const getters= {
    role(state){
        return state.roleList
    }
}

export default {
    state,
    mutations,
    actions,
    getters,
    namespaced:true
}


