import { reqSpcesList, reqSpcesConut } from '../../util/request'

const state = {
    list: [],
    size: 2,
    page: 1,
    total: 0

}
const mutations = {
    changeSpcesList(state, arr) {
        state.list = arr
    },
    changeSpcesTotal(state, num) {
        state.total = num
    },
    changeSpcesPage(state, page) {
        state.page = page
    }
}

const actions = {
    requsetSpcesList(context,bool) {
        let ps = bool?{}:{ page: context.state.page, size: context.state.size }
        reqSpcesList(ps).then((res) => {
            if (res.data.code === 200) {

                let list = res.data.list ? res.data.list : [];

                //如果去到了 空数组，并且当前页不是第一页，那么重新请求前一页
                if (list.length == 0 && context.state.page > 1) {
                    context.commit("changeSpcesPage", context.state.page - 1)
                    context.dispatch("requsetSpcesList")
                    return;
                }
                list.forEach((item) => {
                    item.attrs = JSON.parse(item.attrs)
                })
                context.commit('changeSpcesList', list)
            }

        });
    },
    requsetSpcesTotal(context) {
        reqSpcesConut().then(res => {
            if (res.data.code == 200) {
                context.commit('changeSpcesTotal', res.data.list[0].total)
            }

        })
    },
    changePageAction(context, page) {
        context.commit('changeSpcesPage', page)
        context.dispatch('requsetSpcesList')
    }
}

const getters = {
    spces(state) {
        return state.list
    },
    total(state) {
        return state.total
    },
    size(state) {
        return state.size
    }
}

export default {
    state,
    mutations,
    actions,
    getters,
    namespaced: true
}


