import {reqSeckList} from '../../util/request'

const state= {
    list:[]
    
}
const mutations= {
    changeSeckList(state,arr){
        state.list=arr
    }
}

const actions= {
    requsetSeckList(context){
        reqSeckList().then((res)=>{
            context.commit('changeSeckList',res.data.list)
        })
    }
}

const getters= {
    seck(state){
        return state.list
    }
}

export default {
    state,
    mutations,
    actions,
    getters,
    namespaced:true
}


