import {reqMemberList} from '../../util/request'

const state= {
    memberList:[]
    
}
const mutations= {
    changeMemberList(state,arr){
        state.memberList=arr
    }
}

const actions= {
    requsetMemberList(context){
        reqMemberList().then((res)=>{
            context.commit('changeMemberList',res.data.list)
        })
    }
}

const getters= {
    member(state){
        return state.memberList
    }
}

export default {
    state,
    mutations,
    actions,
    getters,
    namespaced:true
}


