import {reqLoopList} from '../../util/request'

const state= {
    loopList:[]
    
}
const mutations= {
    changeLoopList(state,arr){
        state.loopList=arr
    }
}

const actions= {
    requsetLoopList(context){
        reqLoopList().then((res)=>{
            context.commit('changeLoopList',res.data.list)
        })
    }
}

const getters= {
    loop(state){
        return state.loopList
    }
}

export default {
    state,
    mutations,
    actions,
    getters,
    namespaced:true
}


